.card-container {
  flex-direction: column;
  align-items: flex-start;
  gap: 1rem;
  display: flex;
}

.card {
  opacity: 0;
  background-color: #17a0e4;
  border: 1px solid #000;
  border-radius: .25rem;
  padding: .5rem;
  transition: all .15s;
  transform: translateX(100px);
}

.card.show {
  opacity: 1;
  transform: translateX(0);
}

